import fetch from 'isomorphic-unfetch'

const MainImage = (props) => (
  <div>
    <p>main image view</p>
    <p>
      {props.images.data.data.full_url}
    </p>
  </div>
)

MainImage.getInitialProps = async function() {
  const res = await fetch('http://localhost:7000/uploads/_/originals')
  const data = await res.json()
  console.log(data);
  return { images: data }
}

export default MainImage;